import {actionTypes, reduceHP} from './actions'
import {bindActionCreators} from 'redux'

export const tileNames = {
    mountains: "mountains",
    swamp: "swamp",
    tank: "tank",
    _3hp: "3hp",
    _2hp: "2hp",
    _1hp: "1hp",
    empty: ""
};

export function getmapTile(type) {
    switch (type.toLowerCase()) {
        case tileNames.mountains:
            return(
                {
                    backgroundImage: "url('images/game/gametiles.svg')",
                    backgroundPosition: "-50px 0px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        case tileNames.swamp:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "-100px 0px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        case tileNames.tank:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "-150px 0px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        case tileNames._3hp:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "0px -50px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        case tileNames._2hp:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "-50px -50px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        case tileNames._1hp:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "-100px -50px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
        default:
            return(
                {
                    backgroundImage: "url('../images/game/gametiles.svg')",
                    backgroundPosition: "0px 0px",
                    left: 0,
                    height: 50,
                    width: 50,}
            );
    }
}

const initialState = {
    size: 5,
    map:[
        [
            getmapTile(tileNames.empty),
            getmapTile(tileNames.mountains),
            getmapTile(tileNames.mountains),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.tank)
        ],
        [
            getmapTile(tileNames._1hp),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.mountains),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.empty)
        ],
        [
            getmapTile(tileNames._3hp),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.swamp),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.empty)
        ],
        [
            getmapTile(tileNames.empty),
            getmapTile(tileNames.mountains),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.empty),
            getmapTile(tileNames.empty)
        ],
        [
            getmapTile(tileNames.empty),
            getmapTile(tileNames.mountains),
            getmapTile(tileNames.empty),
            getmapTile(tileNames._2hp),
            getmapTile(tileNames.empty)
        ]
    ]
};

export const mapReducer = (state = initialState, action) =>{
    switch (action.type) {
        case "REDUCE_HP": {
            let map = state.map.slice();
            let {i,j} = action.payload;
            console.log(`${i} ${j}`);
            let img = map[i][j];

            switch (JSON.stringify(img)) {
                case JSON.stringify(getmapTile(tileNames._3hp)):
                    map[i][j] = getmapTile(tileNames._2hp);
                    return {...state, map: map};

                case JSON.stringify(getmapTile(tileNames._2hp)):
                    map[i][j] = getmapTile(tileNames._1hp);
                    return {...state, map: map};

                case JSON.stringify(getmapTile(tileNames._1hp)):
                    map[i][j] = getmapTile(tileNames.empty);
                    return {...state, map: map};
            }
            return state
        }
    }
    return state
};

export const putStateToProps = (state) =>{
    return {
        map: state.map,
        size: state.size
    }
};

export const putActionsToProps = (dispatch) => {
    return {
        reduceHP: bindActionCreators(reduceHP, dispatch)
    }
};



