// ========================================

class MenuGame extends Component {
    constructor(props){
        super(props);
        this.state={
            gunnerSelectedIndex: 0,
            driverSelectedIndex: 0,
            chargerSelectedIndex: 0
        };
    }

    handleChangeButton(index, className){
        this.setState({
            gunnerSelectedIndex: className === "gunner" ? index : this.state.gunnerSelectedIndex,
            driverSelectedIndex: className === "driver" ? index : this.state.driverSelectedIndex,
            chargerSelectedIndex: className === "charger" ? index : this.state.chargerSelectedIndex,
        });
    }

    render() {
        return (
            <div  className="flex-row flex-align-start ">
                <div id="commandsDiv" className="overflow overflow-y-visible margin-10">
                    <GameTable
                        gunnerSelectedIndex={this.state.gunnerSelectedIndex}
                        driverSelectedIndex={this.state.driverSelectedIndex}
                        chargerSelectedIndex={this.state.chargerSelectedIndex}
                    />
                </div>
                <div >
                    <GameButtonsTable onClick={this.handleChangeButton.bind(this)} />
                </div>
            </div>
        )
    }
}

class GameTable extends Component{
    constructor(props){
        super(props);
        this.state={
            rows: [{
                gunner: null,
                driver: null,
                charger: null
            }]
        }
    }

    handleAddTD(){
        const rows = this.state.rows.slice();
        this.setState({
            rows: rows.concat([
                {
                    gunner: null,
                    driver: null,
                    charger: null
                }
            ])
        })
    }

    handleClickTD_LMB(className , indexRow ){
        let rows = this.state.rows.slice();

        rows[indexRow]={
            gunner: className === "gunner" ? getGameButtonImage("gunner",this.props.gunnerSelectedIndex ) : rows[indexRow].gunner,
            driver: className === "driver" ? getGameButtonImage("driver",this.props.driverSelectedIndex ) : rows[indexRow].driver,
            charger: className === "charger" ? getGameButtonImage("charger",this.props.chargerSelectedIndex ) : rows[indexRow].charger
        };

        while (
            rows[rows.length-1].gunner == null &&
            rows[rows.length-1].driver == null &&
            rows[rows.length-1].charger == null)
        {
            rows.pop();
        }
        rows = rows.concat([
            {
                gunner: null,
                driver: null,
                charger: null
            }]);

        this.setState({
            rows: rows
        });
    }

    handleClickTD_RMB(className , indexRow ){
        let rows = this.state.rows.slice();

        rows[indexRow]={
            gunner: className === "gunner" ? null : rows[indexRow].gunner,
            driver: className === "driver" ? null : rows[indexRow].driver,
            charger: className === "charger" ? null : rows[indexRow].charger
        };

        while (rows.length !== 0 &&
        rows[rows.length-1].gunner == null &&
        rows[rows.length-1].driver == null &&
        rows[rows.length-1].charger == null)
        {
            rows.pop();
        }
        rows = rows.concat([
            {
                gunner: null,
                driver: null,
                charger: null
            }]);

        this.setState({
            rows: rows
        });
    }


    renderRows(){
        let arr=[];
        for(let i = 0; i < this.state.rows.length; i++){
            arr.push(
                <GameTableRow
                    index={i}
                    value={this.state.rows[i]}
                    onClickTD_LMB={(className) => this.handleClickTD_LMB(className, i)}
                    onClickTD_RMB={(className) => this.handleClickTD_RMB(className, i)}
                />
            )
        }

        return arr;
    }

    render() {
        return (
            <table id="commands">
                <tr>
                    <th>#</th>
                    <th>Наводчик</th>
                    <th>Водитель</th>
                    <th>Заряжающий</th>
                </tr>
                {this.renderRows()}
            </table>
        )
    }
}


class GameTableRow extends Component{
    index = 0;

    constructor(props) {
        super(props);

        this.index = props.index;
    }

    render() {
        return (
            <tr>
                <td style={{width: 20}} >{this.index+1}</td>
                <GameTableCell
                    value={this.props.value.gunner}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("gunner")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("gunner")}
                />
                <GameTableCell
                    value={this.props.value.driver}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("driver")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("driver")}
                />
                <GameTableCell
                    value={this.props.value.charger}
                    onClickTD_LMB={() => this.props.onClickTD_LMB("charger")}
                    onClickTD_RMB={() => this.props.onClickTD_RMB("charger")}
                />
            </tr>
        );
    }
}

function GameTableCell(props) {
    return (
        <td className="gameTD" onClick={() => props.onClickTD_LMB()} onContextMenu={(e) => {e.preventDefault(); props.onClickTD_RMB()} }>
            <div style={props.value} />
        </td>
    );
}

// ========================================

const gunnerBtnsCount =8;
const driverBtnsCount =11;
const chargerBtnsCount =2;

class GameButtonsTable extends Component{

    onClickButton = null;

    constructor(props){
        super(props);
        this.onClickButton = props.onClick;

        this.state={
            gunnerSelectedIndex: 0,
            driverSelectedIndex: 0,
            chargerSelectedIndex: 0
        }
    }

    renderButtons(className) {
        let arr = [];

        switch (className) {
            case "gunner": {
                let selectIndex = this.state.gunnerSelectedIndex;
                for (let i = 0; i < gunnerBtnsCount; i++) {
                    arr.push(
                        <GameButton index={i} class={className} selected={selectIndex === i}
                                    onClick={() => this.handleClick(i, className)}/>
                    );
                }
                break;
            }
            case "driver":{
                let selectIndex = this.state.driverSelectedIndex;
                for (let i = 0; i < driverBtnsCount; i++) {
                    arr.push(
                        <GameButton index={i} class={className} selected={selectIndex === i}
                                    onClick={() => this.handleClick(i, className)}/>
                    );
                }
                break;
            }
            case "charger":{
                let selectIndex = this.state.chargerSelectedIndex;
                for (let i = 0; i < chargerBtnsCount; i++) {
                    arr.push(
                        <GameButton index={i} class={className} selected={selectIndex === i}
                                    onClick={() => this.handleClick(i, className)}/>
                    );
                }
                break;
            }

        }
        return arr;
    }

    handleClick(index, className){
        this.onClickButton(index, className);

        this.setState({
            gunnerSelectedIndex: className === "gunner" ? index : this.state.gunnerSelectedIndex,
            driverSelectedIndex: className === "driver" ? index : this.state.driverSelectedIndex,
            chargerSelectedIndex: className === "charger" ? index : this.state.chargerSelectedIndex
        })
    }

    render() {
        return (
            <div className="flex-row flex-align-start overflow">
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Наводчик</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("gunner")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20}} className="padding-5">Водитель</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("driver")}
                    </div>
                </div>
                <div className="flex-column">
                    <div style={{height: 20, }} className="padding-5">Заряжающий</div>
                    <div className="gameButtonsColumn flex-row flex-wrap">
                        {this.renderButtons("charger")}
                    </div>
                </div>
            </div>
        )
    }
}

function GameButton(props) {
    return (
        <button className={props.class + (props.selected? " selected":"") + " margin-1"}
                style={getGameButtonImage(props.class, props.index)}
                onClick={props.onClick}/>
    )
}

