import React, {Component} from "react";
import { connect, Provider } from 'react-redux'
import {putStateToProps, putActionsToProps} from '../store/reducers'
import {storeMap} from '../store/stores'
import '../styles/game.css';


class Map extends Component {
    render() {
        return (
            <div id="mapCont" className="overflow-visible">
                <Provider store={storeMap}>
                    <WrappedComponent />
                </Provider>
            </div>
        )
    }
}

class MapTable extends Component {

    renderTable(){
        const {map, size, reduceHP} = this.props;

        let rows = [];

        for (let i = 0; i < size; i++){
            let row = [];
            for (let j = 0; j < size; j++) {
                row.push(
                    <MapTile key={i + "-" + j} value={map[i][j]} onClick={() => reduceHP(i,j)} />
                );
            }
            rows.push(
                <div className="flex-row">
                    {row}
                </div>
            )
        }


        return rows;
    }

    render() {
        return (
                <div id="map" style={{marginTop: 10, marginLeft: 10}}>
                    {this.renderTable()}
                </div>
        )
    }
}

function MapTile(props) {
    return(
        <div className="mapTile" style={props.value} onClickCapture={()=>props.onClick()}>

        </div>
    )
}

const WrappedComponent = connect(putStateToProps, putActionsToProps)(MapTable);


export default Map